import { useEffect, useState } from "react";
import { Results } from "./Results";
import { AnswerQuestion } from "./AnswerQuestion";
import { getLocalStorageItem } from "../utils/localstorage";
import { PanelQuestions } from "./PanelQuestions";

export const MainContentBox = ({ question, user, refreshQuestion }) => {
  // Todo: see if user has answered question

  const [selectedAnswerOption, setSelectedAnswerOption] = useState(null);

  const updateSelectedAnswerOptionFromLocalStorage = () => {
    const answerOption = getLocalStorageItem(question.id);
    setSelectedAnswerOption(answerOption ?? null);
  }

  const updateSelectedAnswerOption = () => {
    const selectedAnswer = question.answers.find(a => a?.external_user_id === user?.id)
    if (selectedAnswer) {
      setSelectedAnswerOption(selectedAnswer.answer_option_id)
    } else {
      setSelectedAnswerOption(null)
    }
  }

  useEffect(() => {
    if (user && question) {
      updateSelectedAnswerOption();
    } else {
      updateSelectedAnswerOptionFromLocalStorage();
    }
  }, [question, user])
  
  return <div>
    {/* Todo: Add cross */}
    <div style={{ marginBottom: '8px', marginTop: '8px' }}>
      <span style={{ fontSize: 'medium' }}>{ question.title }</span>
    </div>
    { selectedAnswerOption ? 
      <div>
        <Results question={question} answerOptionId={selectedAnswerOption}></Results>
        {/* <PanelQuestions panelId={question.panelId} questionId={question.id}></PanelQuestions> */}
      </div> : <AnswerQuestion user={user} onClick={(ao) => setSelectedAnswerOption(ao)} question={question} handleAnswer={refreshQuestion}></AnswerQuestion>
    }
  </div>
}