import { PRIMARY_COLOR } from "../../utils/theme"

export const Button = ({ onClick, href, text }) => {
  return onClick ? <button
    onClick={onClick}
    style={{
      borderRadius: '16px',
      borderWidth: '1px',
      borderColor: PRIMARY_COLOR,
      backgroundColor: 'white',
      color: PRIMARY_COLOR,
      padding: '10px',
      display: "flex",
      flexDirection: "row",
      alignItems: "center",
      justifyContent: "center",
      width: '100%'
    }}
  >{text}</button> : <a href={href} style={{
    borderRadius: '16px',
    borderWidth: '1px',
    borderColor: PRIMARY_COLOR,
    backgroundColor: 'white',
    color: PRIMARY_COLOR,
    padding: '10px',
    display: "flex",
    flexDirection: "row",
    alignItems: "center",
    justifyContent: "center",
    width: '100%'
  }} target="_blank">{text}</a>
}